/* Default icon options */
.leaflet-default-icon-icon {
	background-image: url(//127.0.0.1:8080/2273e3d8ad9264b7daa5bdbf8e6b47f8.png), url(//127.0.0.1:8080/401d815dc206b8dc1b17cd0e37695975.png); /* normal[, Retina] */
	cursor: url(//127.0.0.1:8080/2273e3d8ad9264b7daa5bdbf8e6b47f8.png), url(//127.0.0.1:8080/401d815dc206b8dc1b17cd0e37695975.png), auto; /* normal[, Retina], auto for compliance with cursor syntax */
	width: 25px;
	height: 41px;
	margin: -41px -12px; /* margin top and left to reversely position iconAnchor */
	}
.leaflet-default-icon-shadow {
	background-image: url(//127.0.0.1:8080/44a526eed258222515aa21eaffd14a96.png); /* normal[, Retina] */
	cursor: url(//127.0.0.1:8080/44a526eed258222515aa21eaffd14a96.png), auto; /* normal[, Retina], auto */
	width: 41px;
	height: 41px;
	margin: -41px -12px; /* margin top and left to reversely position shadowAnchor */
	}
.leaflet-default-icon-popup {
	margin: -34px 1px; /* margin top and left to position popupAnchor */
	}
.leaflet-default-icon-tooltip {
	margin: -28px 16px; /* margin top and left to position tooltipAnchor, even if direction 'bottom' or 'right' */
	}
